body {
  background-color: #f0f0f0;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

.search-form {
  margin-bottom: 20px;
  display: flex;
}

.search-form input {
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.search-form button:hover {
  background-color: #0056b3;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: 300px;
  background-color: #fff;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.photo-card img {
  width: 100%;
  height: auto;
}

.info {
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.info-item {
  color: #555;
  font-size: 14px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  display: none;
}

.load-more.show {
  display: block;
}

.load-more:hover {
  background-color: #0056b3;
}

